// Get upcoming workshops from meetup
$(function() {
  // API URI
  eventbrite_url = 'https://www.eventbriteapi.com/v3/organizations/266418941811/events/?status=live&token=HS4VPDV6QYZK3J7B5BS3';

  // Go get 'em!
  $.getJSON(eventbrite_url, function(response) {
    // Grab the HTML element we are going to populate
    var nextWorkshopDetail = $('#next-workshop-detail');

    // Check we actually got a response
    if (response.events.length) {
      // We did! Let's show the details of the very next workshop
      var nextWorkshop = response.events[0]; // The next workshop
      var affiliateTag = 'aff=Website'; // Track affiliate link on Eventbrite

      // Display the deets
      nextWorkshopDetail.append(
        '<p>' +
          '<a target="_blank" href="' + nextWorkshop.url + '?' + affiliateTag + '">' +
          '<img src="' + nextWorkshop.logo.original.url + '" ' +
          'alt="' + nextWorkshop.name.text + '" ' +
          'title="' + nextWorkshop.name.text + '" ' +
          'class="img-fluid">' +
          '</a>' +
        '</p>' +
        '<h4 class="mt-0">' + nextWorkshop.name.html + ' ' +
        '<small>' + moment(nextWorkshop.start.local).format('dddd, MMMM Do YYYY') + ' ' + 
          moment(nextWorkshop.start.local).format('h:mmA') + ' - ' + 
          moment(nextWorkshop.end.local).format('h:mmA') + '</small></h4>' +
        '<p>' + nextWorkshop.summary + '</p>' + 
        '<p><a target="_blank" href="' + nextWorkshop.url + '?' + affiliateTag + '">Find out more information and get tickets</a></p>'
      );

      // Now display all the upcoming workshops in the table
      var tbody = $('#workshops tbody');
      for(var event in response.events) {
        var workshop = response.events[event];
        var startDate = moment(workshop.start.local);
        var endDate = moment(workshop.end.local);
        tbody.append(
          '<tr>' +
            '<td>' + 
              startDate.format('dddd, MMMM Do YYYY') + '<br>' +
              startDate.format('h:mmA') + ' - ' + endDate.format('h:mmA') +
            '</td>' +
            '<td>' + 
              '<dl>' +
                '<dt>' + workshop.name.text + '</dt>' +
                '<dd>' +
                  '<a href="' + workshop.url + '?' + affiliateTag + '" target="_blank">Get tickets</a>' +
                '</dd>' +
              '</dl>' +
            '</td>' +
          '</tr>'
        );
      }
    } else {
      // If no events then show a nice message
      nextWorkshopDetail.append(
        '<p class="lead">Fill in the form below and we\'ll let you know when our next workshop is scheduled.</p>' +
        '<p>We\'ll give you advance notice so you can grab a spot before anyone else does. We may even offer a ' +
        'discount.</p>' +
        '<div id="mc_embed_shell">' +
        '  <link href="//cdn-images.mailchimp.com/embedcode/classic-061523.css" rel="stylesheet" type="text/css">' +
        '  <div id="mc_embed_signup">' +
        '    <form action="https://deskandstudio.us16.list-manage.com/subscribe/post?u=d0e4c867dec811a28e6e98606&amp;id=1aa582eba0&amp;f_id=006055e0f0" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" class="validate" target="_blank">' +
        '      <div id="mc_embed_signup_scroll"><h4>Let me know about the next workshop</h4>' +
        '        <div class="indicates-required"><span class="asterisk">*</span> indicates required</div>' +
        '        <div class="mc-field-group"><label for="mce-MMERGE4">Name </label><input type="text" name="MMERGE4" class=" text" id="mce-MMERGE4" value=""></div><div class="mc-field-group"><label for="mce-EMAIL">Email Address <span class="asterisk">*</span></label><input type="email" name="EMAIL" class="required email" id="mce-EMAIL" required="" value=""></div><div class="mc-field-group"><label for="mce-MMERGE3">Phone </label><input type="number" name="MMERGE3" class=" number" id="mce-MMERGE3" value=""></div>' +
        '        <div hidden=""><input type="hidden" name="tags" value="3495603"></div>' +
        '        <div id="mce-responses" class="clear foot">' +
        '          <div class="response" id="mce-error-response" style="display: none;"></div>' +
        '          <div class="response" id="mce-success-response" style="display: none;"></div>' +
        '        </div>' +
        '        <div aria-hidden="true" style="position: absolute; left: -5000px;">' +
        '          /* real people should not fill this in and expect good things - do not remove this or risk form bot signups */' +
        '          <input type="text" name="b_d0e4c867dec811a28e6e98606_1aa582eba0" tabindex="-1" value="">' +
        '        </div>' +
        '        <div class="optionalParent">' +
        '          <div class="clear foot">' +
        '            <input type="submit" name="subscribe" id="mc-embedded-subscribe" class="button" value="Let me know">' +
        '            <p style="margin: 0px auto;"><a href="http://eepurl.com/iPGw4o" title="Mailchimp - email marketing made easy and fun"><span style="display: inline-block; background-color: transparent; border-radius: 4px;"><img class="refferal_badge" src="https://digitalasset.intuit.com/render/content/dam/intuit/mc-fe/en_us/images/intuit-mc-rewards-text-dark.svg" alt="Intuit Mailchimp" style="width: 220px; height: 40px; display: flex; padding: 2px 0px; justify-content: center; align-items: center;"></span></a></p>' +
        '          </div>' +
        '        </div>' +
        '      </div>' +
        '    </form>' +
        '  </div>' +
        '  <script type="text/javascript" src="//s3.amazonaws.com/downloads.mailchimp.com/js/mc-validate.js"></script><script type="text/javascript">(function($) {window.fnames = new Array(); window.ftypes = new Array();fnames[4]=\'MMERGE4\';ftypes[4]=\'text\';fnames[0]=\'EMAIL\';ftypes[0]=\'email\';fnames[3]=\'MMERGE3\';ftypes[3]=\'number\';fnames[1]=\'FNAME\';ftypes[1]=\'text\';fnames[2]=\'LNAME\';ftypes[2]=\'text\';}(jQuery));var $mcj = jQuery.noConflict(true);</script>' +
        '</div>'
      );
    }
  });
});
